body.display_menu {
  overflow-y: hidden;
}
header {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  z-index: 1;
  padding: 10px 0;
  background-color: #FAFBFC;
  position: fixed;
  width: 100%;
  top: 0;
  -webkit-box-pack: center;
      -ms-flex-pack: center;
          justify-content: center;
  -webkit-box-shadow: 0 0 10px 0 rgba(0, 0, 0, 0.1);
          box-shadow: 0 0 10px 0 rgba(0, 0, 0, 0.1);
  -webkit-transition: 0.33s;
  transition: 0.33s;
}
header .wrap {
  padding: 0 2%;
  width: 100%;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-align: end;
      -ms-flex-align: end;
          align-items: flex-end;
  -webkit-box-pack: justify;
      -ms-flex-pack: justify;
          justify-content: space-between;
}
header img {
  width: 100px;
}
header #menu {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-orient: horizontal;
  -webkit-box-direction: normal;
      -ms-flex-direction: row;
          flex-direction: row;
}
header #menu li {
  position: relative;
  -webkit-user-select: none;
     -moz-user-select: none;
      -ms-user-select: none;
          user-select: none;
  margin: 0 30px;
}
header #menu li i {
  width: 12px;
  height: 12px;
  background-color: #3E3A37;
}
header #menu li a {
  color: #3E3A37;
  cursor: pointer;
  font-size: 1.15em;
  border: none;
  border-bottom: 2px solid #ffffff;
  -webkit-transition: 0.15s;
  transition: 0.15s;
  background: none;
}
header #menu li a:hover {
  border-bottom-color: #ff4401;
  color: #ff4401;
}
header #menu li a:hover i {
  background-color: #ff4401;
}
header .drop_menu {
  position: absolute;
  display: block;
  top: 149%;
  -webkit-transform: scaleY(0);
          transform: scaleY(0);
  width: auto;
  -webkit-transform-origin: top;
          transform-origin: top;
  background-color: #ffffff;
  -webkit-transition: 0.25s;
  transition: 0.25s;
  padding-bottom: 10px;
  -webkit-box-shadow: 0 2px 5px 0 rgba(0, 0, 0, 0.1);
          box-shadow: 0 2px 5px 0 rgba(0, 0, 0, 0.1);
}
header .drop_menu a {
  margin-left: 20px;
  display: block;
  -webkit-transition: 0.45s;
  transition: 0.45s;
  opacity: 0;
  margin-right: 20px;
  padding: 10px 0 0 0;
}
header .drop_menu.display {
  -webkit-transform: scaleY(1);
          transform: scaleY(1);
}
header .drop_menu.display a {
  opacity: 1;
}
header #hamburger {
  cursor: pointer;
  border-radius: 50%;
  position: absolute;
  left: 3%;
  top: 30px;
  display: none;
  -webkit-transform: translateY(-50%);
          transform: translateY(-50%);
}
header #hamburger span {
  height: 2px;
  margin-top: 5px;
  margin-bottom: 5px;
  background-color: #3E3A37;
  display: block;
  -webkit-transition: 0.33s;
  transition: 0.33s;
}
header #hamburger span:nth-child(1) {
  width: 12px;
}
header #hamburger span:nth-child(2) {
  width: 24px;
}
header #hamburger span:nth-child(3) {
  width: 12px;
}
.display_menu header #hamburger span:nth-child(1) {
  -webkit-transform: rotate(45deg) translate(2px, 1px);
          transform: rotate(45deg) translate(2px, 1px);
}
.display_menu header #hamburger span:nth-child(2) {
  -webkit-transform: rotate(-45deg);
          transform: rotate(-45deg);
}
.display_menu header #hamburger span:nth-child(3) {
  -webkit-transform: rotate(45deg) translate(6px, -9px);
          transform: rotate(45deg) translate(6px, -9px);
}
.login_bar {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
}
@media screen and (max-width: 660px) {
body {
    padding-top: 70px;
}
header .wrap {
    -webkit-box-pack: center;
        -ms-flex-pack: center;
            justify-content: center;
    -webkit-box-align: center;
        -ms-flex-align: center;
            align-items: center;
    -webkit-box-orient: vertical;
    -webkit-box-direction: normal;
        -ms-flex-direction: column;
            flex-direction: column;
    padding: 0;
}
header img {
    width: 50px;
}
header #hamburger {
    display: block;
}
header #menu {
    width: 100%;
    display: block;
    height: 0;
    -webkit-transform-origin: 50% 0;
            transform-origin: 50% 0;
    -webkit-transition: 0.33s ease;
    transition: 0.33s ease;
    -webkit-box-orient: vertical;
    -webkit-box-direction: normal;
        -ms-flex-direction: column;
            flex-direction: column;
}
.display_menu header #menu {
    height: calc(100vh - 64px);
}
.display_menu header #menu li {
    height: calc((100vh - 113px)/8);
    border-bottom: 1px solid #DAD9D7;
    -webkit-transition: 0.25s ease;
    transition: 0.25s ease;
    opacity: 1;
    display: block;
}
header #menu li {
    height: 0;
    opacity: 0;
    margin-left: 0;
    -webkit-transition: 0.25s ease;
    transition: 0.25s ease;
}
header #menu li a {
    left: 0;
    line-height: calc((100vh - 113px)/8);
    padding-left: 20px;
    border: none;
    height: 100%;
    width: 100%;
    display: block;
}
header #menu li a:hover {
    color: #ffffff;
    background-color: #ff4401;
}
header #menu li a:hover i {
    background-color: #ffffff;
}
header #menu li i {
    position: absolute;
    right: 20px;
    top: 50%;
    -webkit-transform: translateY(-50%);
            transform: translateY(-50%);
    background-color: #3E3A37;
}
header .drop_menu {
    top: calc((100vh - 113px)/8);
    -webkit-box-shadow: none;
            box-shadow: none;
    left: 0;
    padding-bottom: 0;
    width: 100%;
}
header .drop_menu a {
    width: 100%;
    padding: 0 !important;
    padding-left: 40px !important;
    margin: 0;
    border-bottom: 1px solid #DAD9D7 !important;
}
.login_bar {
    display: inline;
}
}
@media screen and (max-width: 660px) and (max-height: 500px) {
.display_menu header {
    max-height: 100vh;
    overflow-y: scroll;
}
.display_menu header #menu li {
    height: calc((100vh - 113px)/4);
}
header #menu li a {
    line-height: calc((100vh - 113px)/4);
}
header .drop_menu {
    top: calc((100vh - 113px)/4);
}
}
